* {
  box-sizing: border-box; }

//  Typography
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: $color-blue;
  font-weight: normal;
  font-family: 'OpenSansRegular';
  text-decoration: none;
  margin: 0 0 20px 0; }

.h1, h1 {
  font-size: rem(26);
  line-height: 1.2;
  letter-spacing: -1px;
  color: $color-blue; }

.h2, h2 {
  font-size: rem(20);
  line-height: 1.14; }

.h3, h3 {
  font-size: rem(16);
  line-height: 1.38; }

.h4, h4 {
  font-size: rem(14);
  line-height: 1.38; }

.h5, h5 {
  font-size: rem(13); }

.h6, h6 {
  font-size: rem(12); }

.small, small {
  font-size: rem(12);
  line-height: 1.2; }

p {
  margin: 0 0 15px 0;
  line-height: 24px; }

em {
  font-style: normal;
  font-family: 'OpenSansItalic'; }

strong {
  font-weight: normal;
  font-family: 'OpenSansSemiBold'; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0; }

// Links
a {
  color: $link-color;
  text-decoration: underline;
  outline: 0;
  transition: color 0.3s linear, background-color 0.3s linear; }

a:hover, a:focus, a:active {
  color: lighten($link-color, 10%);
  text-decoration: underline;
  outline: none; }

.link {
  text-decoration: underline;
  i {
    margin-left: 10px;
    font-size: rem(18);
    vertical-align: middle; }
  &--reverse {
    text-decoration: none; }

  &--back {
    i {
      margin-right: 20px;
      font-size: 16px;
      font-weight: bold;
      margin-left: 0;
      &.zmdi-arrow-left {
        vertical-align: inherit; } } } }

a:hover.link {
  text-decoration: none;
  &--reverse {
    text-decoration: underline; } }

