.jq-selectbox {
  width: 100%;
  &__select {
    height: 36px;
    background: none;
    text-shadow: none;
    border-color: $color-blue;
    padding-left: 20px;
    padding-right: 60px;
    box-shadow: none;
    font-size: 16px;
    font-family: "OpenSansRegular",sans-serif;
    color: $color-blue;
    &:active, &:hover {
      background: none;
      box-shadow: none; } }

  &__trigger {
    border-left-color: $color-blue;
    width: 48px;
    &-arrow {
      border: 0;
      font: normal normal normal 26px/1 'Material-Design-Iconic-Font';
      line-height: 0.5;
      top: 5px;
      right: 30px;
      &:after {
        content: '\f2f9';
        display: block;
        line-height: 1; } } }

  &__select-text {
    width: 100% !important;
    height: 100%; }

  &__dropdown {
    border-radius: 0;
    font-size: 16px;
    font-family: "OpenSansRegular",sans-serif;
    margin-top: 0; }
  li {
    height: 36px;
    padding: 0 10px;
    line-height: 36px; } }



.navigation__category__select {
  width: 100%;
  max-width: 345px;
  margin: 0 auto 30px;
  display: block !important; }
